@import url(https://fonts.googleapis.com/css2?family=Fjalla+One&display=swap);
*{
  margin: 0;
  padding: 0;
  scroll-behavior: smooth;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


/* Type Scale */

html {font-size: 100%;} /*16px*/

body {
  font-family: 'Fjalla One', sans-serif;
  font-weight: 400;
  line-height: 1.5;
  color: #000000;

  --text-big3: 5.96rem;
  --text-big2: 4.768rem;
  --text-big1: 3.815rem;
}

h1, h2, h3, h4, h5 {
  font-family: 'Fjalla One', sans-serif;
  font-weight: 400;
  line-height: 1.3;
}

h1 {font-size: 3.052rem;}

h2 {font-size: 2.441rem;}

h3 {font-size: 1.953rem;}

h4 {font-size: 1.563rem;}

h5 {font-size: 1.25rem;}

small, .text_small {font-size: 0.8rem;}

a{ text-decoration: none; }


/* Color Scheme */

body{
  /* Colors */
  --black-fogra: #0A1E27;
  --dark-black-fogra: #051116;
  --bright-yellow: #FCA61D;
  --princeton-orange: #F5812A;
  --cinnabar: #E64D2D;
  --floral-white: #FFFCF7;
}

/* List Style */
ul{
  list-style: none;
}

/* Buttons */
button{
  outline: none;
  border: none;
}
/* background: linear-gradient(to bottom right, #333 25%, #fff 0%); */
/* ===== Home ===== */
#home{
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  height: 100vh;
  width: 100%;
  overflow: hidden;
  background-color: var(--floral-white);

  --home-p-size: 20px;
  --home-h1-size: 80px;
  --home-content-height: 250px;
}
.home__nav,
.home__content,
.home__links{
  z-index: 10;
}

/* ==== Content ==== */
.home__content{
  position: absolute;
  height: var(--home-content-height);
}
.home__content h1{
  letter-spacing: 5px;
  font-size: var(--home-h1-size);
  color: var(--dark-black-fogra);
}
.home__content p{
  letter-spacing: 6px;
  font-size: var(--home-p-size);
  color: var(--dark-black-fogra);
}
.home__content button{
  margin: 20px 0px;
  padding: 8px 10px;
  border-radius: 5px;
  letter-spacing: 1px;
  font-family: inherit;
  font-size: var(--home-p-size);
  color: var(--dark-black-fogra);
}
.home__content button i{
  padding: 0px 8px;
  font-size: 25px;
}
/*
  Button Animation 
  https://fossheim.io/writing/posts/css-text-gradient/ 
*/
.home__content button{
  background-image: linear-gradient(
    45deg, 
    var(--princeton-orange) 50%, /* Current background color */
    var(--dark-black-fogra) 50% /* Background color on hover */
);
  background-size: 270%;
  background-repeat: repeat;
  background-position: top right;
  -webkit-animation: button-animation 0.4s 0.15s ease-out forwards;
          animation: button-animation 0.4s 0.15s ease-out forwards;
  cursor: pointer;
  border: 3px solid var(--dark-black-fogra);
}
.home__content button span{
  background-image: linear-gradient(
    45deg,
    var(--dark-black-fogra) 50%, /* Current text color */
    var(--floral-white) 50%  /* Text color on hover */
  );
  background-size: 270%;
  background-repeat: repeat;
  background-position: top right;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent; 
  -moz-background-clip: text;
  -moz-text-fill-color: transparent;
  -webkit-animation: button-animation 0.4s 0.15s ease-out forwards;
          animation: button-animation 0.4s 0.15s ease-out forwards;
}
button:hover, button:hover span{
  -webkit-animation: button-animation-rev 0.4s ease-out forwards;
          animation: button-animation-rev 0.4s ease-out forwards;
}
button:hover {
  box-shadow: 3px 2px 10px 1px rgba(0,0,0,0.15);
  transition: 0.5s;
}
@-webkit-keyframes button-animation {
  0% {
    background-position: top right;
  }
  100% {
    background-position: top left;
  }
}
@keyframes button-animation {
  0% {
    background-position: top right;
  }
  100% {
    background-position: top left;
  }
}
@-webkit-keyframes button-animation-rev {
  0% {
    background-position: top left;
  }
  100% {
    background-position: top right;
  }
}
@keyframes button-animation-rev {
  0% {
    background-position: top left;
  }
  100% {
    background-position: top right;
  }
}

/* ==== Social Links ==== */
.home__links{
  position: absolute;
  right: 20px;
  top: auto;
  bottom: auto;
  height: var(--home-content-height);
}
.home__links li{
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  margin-bottom: 30px;
  cursor: pointer;
}
.home__links .fab{
  font-size: calc(var(--home-p-size) + 15px);
  color: var(--dark-black-fogra);
}
.home__links .fa-github{
  font-size: 34px !important;
}
.home__links .fa-instagram{
  font-size: 37px !important;
  font-weight: bold;
}
.home__links a,
.home__links i{
  transition: .25s;
}
.home__links a:hover, 
.home__links a:hover i{ 
  font-weight: normal; 
  font-weight: initial;
  -webkit-text-stroke: 2px var(--dark-black-fogra);
  color: var(--princeton-orange);
  -webkit-transform: translateY(-3px);
          transform: translateY(-3px);
}

/* ==== Top Nav Links ==== */
.home__nav{
  position: absolute;
  right: 10px;
  top: 4vh;
}
.home__navList{
  display: -webkit-flex;
  display: flex;
}
.home__navList li{
  margin: 0 10px;
}
.home__navList a{
  position: relative;
  padding-bottom: 5px;
  letter-spacing: 2px;
  cursor: pointer;
  transition: .2s;
}
.home__resumeBtn{
  padding-left: 20px;
  border-left: 2px solid var(--princeton-orange);
}
.home__resumeBtn i{
  margin-right: 10px;
  margin-bottom: -2px;
  transition: .2s;

}
/* Fonts Styles */
.home__resumeBtn i,
.home__navList a{
  font-size: var(--home-p-size);
  color: var(--dark-black-fogra);
}
.home__navList a:hover, 
.home__navList i:hover,
.home__resumeBtn:hover i{ 
  color: var(--princeton-orange);
}


/* ==== Media Queries ==== */

/* Tablet */
@media screen and (max-width: 768px){
  .home__content{ 
    width: 60vw;
    --home-h1-size: 70px;
  }

  .home__navList,
  .home__links{
    display: none;
  }
}
@media screen and (max-width: 425px){
  .home__content{ 
    width: 60vw;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    padding-left: 20px;
    --home-h1-size: 16vw;
    --home-p-size: 5vw;
  }
}
@media screen and (max-width: 375px){
  .home__content button i{ 
    display: none;
  }
}
/* Retro Lines Decoration */
.retroLines{
  position: relative;
  height: 100vh;
  width: 100vw;
}
.retroLines__black{
  position: absolute;
  left: 0;
  bottom: 0;
  height: 100%; /* Height (length) of black lines */
  width: 100%;
}
.retroLines__red,
.retroLines__orange,
.retroLines__yellow{
  position: absolute;
  left: 8px; /* Space in between colored lines */
  bottom: 0;
  height: 90%; /* Height (length) of colored lines */
  width: 100%;
}

/* Line Colors */
.retroLines__black{ background: var(--dark-black-fogra); }
.retroLines__red{ background: var(--cinnabar); }
.retroLines__orange{ background: var(--princeton-orange); }
.retroLines__yellow{ background: var(--bright-yellow); }

/* Inside white space - creates curved line effect */
.retroLines__blackInside,
.retroLines__redInside,
.retroLines__orangeInside,
.retroLines__yellowInside{
  position: absolute;
  left: 1.75vh; /* Vertical colored line width */
  top: -1px;
  height: 96.5%; /* Horizontal colored line height */
  width: 100%;
  background: var(--floral-white); /* Background color */
  overflow: hidden;
}
.retroLines__blackInside{ border-bottom-left-radius: 200px; }
.retroLines__redInside{ border-bottom-left-radius: 190px; }
.retroLines__orangeInside{ border-bottom-left-radius: 180px; }
.retroLines__yellowInside{ border-bottom-left-radius: 170px; }


@media screen and (max-width: 768px){
  .retroLines__blackInside{ border-bottom-left-radius: 160px; }
  .retroLines__redInside{ border-bottom-left-radius: 150px; }
  .retroLines__orangeInside{ border-bottom-left-radius: 140px; }
  .retroLines__yellowInside{ border-bottom-left-radius: 130px; }
}

@media screen and (max-width: 425px){
  .retroLines__red,
  .retroLines__orange,
  .retroLines__yellow{
    left: 1vw; /* Space in between colored lines */
  }

  .retroLines__blackInside,
  .retroLines__redInside,
  .retroLines__orangeInside,
  .retroLines__yellowInside{
    left: 1.5vh;
    height: 97%;
  }

  .retroLines__blackInside{ border-bottom-left-radius: 100px; }
  .retroLines__redInside{ border-bottom-left-radius: 90px; }
  .retroLines__orangeInside{ border-bottom-left-radius: 80px; }
  .retroLines__yellowInside{ border-bottom-left-radius: 70px; }
}
.burger{
    z-index: 20;
    display: none;
    position: absolute;
    top: 4vh;
    right: 30px;
    height: 25px;
    width: 25px;
    cursor: pointer;
    -webkit-appearance: none;
}
.burger span{
    position: absolute;
    top: 45%;
    height: 3px;
    width: 25px;
}
.burger span::before,
.burger span::after{
    content: '';
    position: absolute;
    height: 3px;
    width: 25px;
}
.burger span,
.burger span::before,
.burger span::after{ 
    transition: .35s;
    background: var(--dark-black-fogra); 
}
.burger span::before{
    top: -.6rem;
}
.burger span::after{
    top: .6rem;
}

/* Hover Effect */
.burger:hover span{
    background: var(--princeton-orange);
}
.burger:hover span::before{
    background: var(--bright-yellow);
}
.burger:hover span::after{ 
    background: var(--cinnabar); 
}

/* Small Fixes */

/* Cant see yellow ::before on yellow background */
.burger-active.burger:hover span::before{
    background: var(--cinnabar);
}
/* Make burger follow if scrolled */
.burger-active.burger{
    position: fixed;
}

/* Burger Animation */
.burger-active.burger span{
    background: rgba(0, 0, 0, 0);
}
.burger-active.burger span::before{
    top: 0;
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
}
.burger-active.burger span::after{
    top: 0;
    -webkit-transform: rotate(135deg);
            transform: rotate(135deg);  
}

@media screen and (max-width: 768px){
    .burger{
        display: inline;
        display: initial;
    }
}
.sidebar{
  z-index: 15;
  display: none;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  position: fixed;
  top: 0;
  right: -360px;
  height: 100vh;
  width: 350px;
  background-color: var(--bright-yellow);
  transition: .5s ease;
}
.sidebar-active{ right: 0; }

/* Navigation + Resume */
.sidebar__nav{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  height: 20vh;
  margin-bottom: 10vh;
}
.sidebar__nav a{ 
  cursor: pointer;
}

/* Resume Button */
.sidebar__resume{
  margin-bottom: 10vh;
  cursor: pointer;
}

/* Links */
.sidebar__links{
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  height: 50px;
  width: 50%;
  margin-bottom: 20vh;
}
.sidebar__links li{
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}
.sidebar__links li a{
  padding: 5px 5px;
  cursor: pointer;
}

/* Font sizes */
.sidebar i{ font-size: 35px; } /* Icons */
.sidebar a { font-size: 25px; } /* Text */

/* Hovers: nav links */
.sidebar__nav a:hover,
.sidebar__resume:hover{
  letter-spacing: 2px;
}
/* Hovers: socials */
.sidebar__links a:hover{
  -webkit-transform: translateY(-5px);
          transform: translateY(-5px);
}
.sidebar a{ transition: .2s;}


/* ==== Media Queries ==== */

/* Tablet */
@media screen and (max-width: 768px) {
  .sidebar{
    display: -webkit-flex;
    display: flex;
  }
}
/* Mobile L - S */
@media screen and (max-width: 425px) {
  .sidebar{
    width: 100%;
    right: -100%;
  }
  .sidebar-active{
    right: 0;
  }
  .sidebar__nav{
    height: 25vh;
  }
}

